import React, { useCallback, useState } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { useForm, Controller } from 'react-hook-form';
import classNames from 'classnames/bind';
import { FaExclamationCircle } from 'react-icons/fa';

import { Input, Button, Fieldset, InputNumber, Select } from '../../../components/core';
import styles from './ship_form.module.scss';

import cityAreaData from '../../../api/city_area.json';
import { updateShipInfo } from '../../../store/slice/order';

const cx = classNames.bind(styles);

const phonePattern = /^09\d{2}-?\d{3}-?\d{3}$/;
const cities = Object.keys(cityAreaData);

const ShipForm = (props) => {
  const { step } = props;

  let history = useHistory();
  const dispatch = useDispatch();

  const [cityIdx, setCityIdx] = useState(-1);
  const [areaIdx, setAreaIdx] = useState(-1);

  const {
    control,
    register,
    handleSubmit,
    formState: { errors },
    setValue,
  } = useForm();

  const cityData = useCallback(() => {
    let data = [];
  
    cities.map((item, idx) => {
      data.push({
        value: idx,
        label: item
      });
    });
  
    return data;
  }, []);
  
  const areaData = useCallback((cityIdx) => {
    let data = [];
 
    if (cityIdx >= 0) {
      cityAreaData[cities[cityIdx]].map((item, idx) => {
        const { name, zip } = item;
    
        data.push({
          value: idx,
          label: name,
          zip: zip
        });
      });
    }

    return data;
  }, []);

  const atSubmit = useCallback((data) => {
    if (data) {
      const newData = {
        ...data,
        city: cities[cityIdx],
        area: cityAreaData[cities[cityIdx]][areaIdx].name
      };

      dispatch(updateShipInfo(newData));

      history.push('/checkout/payment');
    }
  }, [cityIdx, areaIdx]);

  return (
    <form className={cx('ship_form')} onSubmit={handleSubmit(atSubmit)}>
      <div className={cx('title_box')}>
        <h2></h2>
        {step}
      </div>
      <div>
        <div className={cx('multiple')}>
          <Fieldset
            label=""
            helperText={errors?.lastName?.message}
            error={errors.lastName}
          >
            <Input
              type="text"
              placeholder=""
              suffix={<FaExclamationCircle />}
              {...register('lastName', { required: '' })}
            />
          </Fieldset>
          <Fieldset
            label=""
            helperText={errors?.firstName?.message}
            error={errors.firstName}
          >
            <Input
              type="text"
              placeholder=""
              suffix={<FaExclamationCircle />}
              {...register('firstName', { required: '' })}
            />
          </Fieldset>
        </div>
        <div>
          <Fieldset
            label=""
            helperText={errors?.phone?.message}
            error={errors.phone}
          >
            <Controller
              control={control}
              name="phone"
              rules={{
                required: '',
                validate: (value) => phonePattern.test(value) || ''
              }}
              render={({ field }) => {
                return (
                  <InputNumber
                    {...field}
                    format="####-###-###"
                    placeholder=""
                    iconErr={<FaExclamationCircle />}
                  />
                );
              }}
            />
          </Fieldset>
        </div>
        <div className={cx('multiple')}>
          <Fieldset
            label=""
            helperText={errors?.city?.message}
            error={errors.city}
          >
            <Controller
              name="city"
              control={control}
              rules={{ required: '' }}
              render={({ field: { onChange } }) => {
                return (
                  <Select
                    placeholder=""
                    options={cityData()} 
                    onChange={(e) => {
                      onChange(e);
                      setValue('area', null);

                      setCityIdx(e.value);
                      setAreaIdx(-1);
                    }}
                  />
                );
              }}
            />
          </Fieldset>
          <Fieldset
            helperText={errors?.area?.message}
            error={errors.area}
          >
            <Controller
              name="area"
              control={control}
              rules={{ required: '' }}
              render={({ field: { onChange, value } }) => {
                return (
                  <Select
                    placeholder=""
                    value={value}
                    options={areaData(cityIdx)} 
                    onChange={(e) => {
                      onChange(e);
                      setAreaIdx(e.value);
                    }}
                  />
                );
              }}
            />
          </Fieldset>
        </div>
        <div>
          <Fieldset
            helperText={errors?.address?.message}
            error={errors.address}
          >
            <Input
              type="text"
              placeholder=""
              suffix={<FaExclamationCircle />}
              {...register('address', { required: '' })}
            />
          </Fieldset>
        </div>
      </div>
      <div>
        <Button type="secondary" block></Button>
      </div>
    </form>
  );
};

export default ShipForm;
